.wrapper {
	height: 100%;
	width: 100%;
	margin: 0 auto;
	max-width: 1000px;
	-webkit-box-shadow: 0px 0px 10px 2px rgba(20, 20, 20, 0.6);
	        box-shadow: 0px 0px 10px 2px rgba(20, 20, 20, 0.6);
	background-color: #333;

	overflow: hidden;
}

.App {
	position: absolute;
	height: 100%;
	width: 100%;

	background-color: #666;
	color: #E0E0E0;
	font-size: large;
	display: -ms-flexbox;
	display: flex;

	-ms-flex-flow: row;

	    flex-flow: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}

.SwiperViewport-container {
	-ms-flex-positive: 10;
	    flex-grow: 10;
	height: calc(100% - 64px);
}
.SwiperViewport-wrapper {
	position: relative;
	height: 100%;
}


