.Notice span {
	padding: 0.5rem 0px;
	width: 100%;
}

.Notice {
	max-height: 36px;
	max-width: 1000px;
	width: 100%;
	height: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: absolute;
	z-index: 1000;
	overflow: hidden;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row;
	    flex-flow: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	text-align: center;
	
	color: red;
	font-size: 1em;

	-webkit-transition: max-height 0.8s ease;

	-o-transition: max-height 0.8s ease;

	transition: max-height 0.8s ease;

	color: white;
	background-color: #509DE0;
}
.Error {
	background-color: #FF5555;
}

.Notice-hidden {
	max-height: 0;
}



