.Header {
	height: 64px;
	width: 100%;
	background-color: #444;
	z-index: 9999;
	position: -webkit-sticky;
	position: sticky;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-flow: row;
	    flex-flow: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;

	-webkit-box-shadow: 0px 2px 5px 1px rgba(20, 20, 20, 0.5);

	        box-shadow: 0px 2px 5px 1px rgba(20, 20, 20, 0.5);
}
.Header .title {
	font-size: 32px;
	line-height: 32px;
}
.nav-button {
	margin: 0 1rem;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row;
	    flex-flow: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}


